* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
@font-face {
    font-family: Alta;
    src: url("../assets/fonts/Alta_regular.otf") format("opentype");
}
@font-face {
    font-family: Amsterdam;
    src: url("../assets/fonts/Amsterdam.otf") format("opentype");
}

:root {
    --mainColor: #A57F40;
    --mainColorLight: white;
    --secondaryColor: gray;
    --textColor: #fff;
    font-family: Alta, sans-serif;
    background-color: white;
}

#root {
    min-height: 100vh;
}

.header-logo {
    position: fixed;
    right: 50%;
    top: 0;
    width: 100px;
    height: 100px;
    transform: translate(50%, 0%) !important;
    filter: drop-shadow(0 0 1rem var(--mainColorLight));
}

header {
    z-index: 999;
    transition: all 0.5s;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100px;
    padding: 0 2rem;
    background-color: #A57F40;
    color: var(--textColor);
    width: 100vw;
}

nav a {
    margin: 0 1rem;
    color: var(--textColor);
    text-decoration: none;
}
nav a:hover {
    text-decoration: underline;
    text-underline-offset: 3px;
}

.blur {
    background-color: unset;
    backdrop-filter: blur(10px);
}

.blur-fix {
    top: 0;
}

.black {
    color: #A57F40;
    background-color: unset;
}

header .nav-btn {
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
}

header div,
nav {
    display: flex;
    align-items: center;
}

div .background {
    position: relative;
    width: 100vw;
    height: 100vh;
    background: url("/public/images/bg.JPG") no-repeat center;
    background-size: cover;
    filter: blur(4px);
    -webkit-filter: blur(4px);
    z-index: -1;
}
div .prestation:first-child {
    margin-top: 5vh;
}

div .prestation {
    margin-top: 7.5vh;
    padding: 2%;
}

.prestation-images {
    display: flex;
    flex-wrap: wrap;
    align-items: start;
    justify-content: start;
}

div .prestation-container {
    display: flex;
    flex-direction: column;
    position: relative;
}

div .prestation-title {
    filter: drop-shadow(1rem 1rem 2rem var(--mainColorLight));
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: Alta, sans-serif;
    font-size: 2rem;
    font-weight: bold;
}

div .prestation-image {
    visibility: hidden;
    opacity: 0;
    min-width: calc(15rem + 5vw);
    min-height: calc(15rem + 5vh);
    max-width: calc(15rem + 5vw);
    max-height: calc(15rem + 5vh);
    box-shadow: 0 0 8px 8px var(--mainColorLight) inset;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 25px;
    flex: 1 1 30%;
}

div .prestation-infos {
    transform: translate(2%, 0%);
    height: 100%;
    padding: 5vh 10vw;
    display: flex;
    justify-content: center;
}
div .prestation-infos > *:not(.prestation-price) {
    margin: unset;
    transition: all 1.5s;
    padding: 0 5%;
}

a {
    color: var(--mainColor);
    text-decoration: none;
}

div .prestation-desc {
    z-index: -1;
    transform: translate(-50%, 0);
    opacity: 0;
    display: flex;
    text-align: center;
    justify-content: center;
    color: var(--mainColor);
    font-size: 1rem;
}

.blank {
    padding-top: 100px;
}

.login-container, .register-container {
    margin-top: 3rem;
    display: inline-flex;
    flex: 1 1 0px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
}

.login, .register {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 55vh;
}

:is(.login, .register) > input {
    border: 1px solid var(--secondaryColor);
    padding: 1rem;
    border-radius: 15px;
}

:is(.login, .register) > * {
    width: 15rem;
}

.login-title-container, .register-title-container {
    margin-bottom: 1rem;
}

.login-title, .register-title {
    width: 5vw;
    border-bottom: 1px solid var(--secondaryColor);
    padding-bottom: 2%;
}

.login-submit, .register-submit {
    font-size: 0.9rem;
    color: var(--mainColorLight);
    background-color: var(--mainColor);
    border-color: var(--secondaryColor);
}

.login-to-register, .register-to-login {
    color: var(--secondaryColor);
    font-size: small;
    font-family: Arial, Helvetica, sans-serif;
    white-space: nowrap;
}

.bouncing-text {
    visibility: hidden;
    position: relative;
    display: flex;
    font-size: 70px;
    font-weight: 900;
    font-family: 'Arial Black', 'Arial Bold', Gadget, sans-serif;
}

.bouncing-text::after {
    content: attr(data-text);
    position: absolute;
    transition: all 1s ease-in;
    bottom: 0;
    right: 0;
    visibility: hidden;
    color: var(--mainColorLight);
    opacity: 0;
    text-shadow: var(--mainColor) 0 0 1px, var(--mainColor) 0 0 1px, var(--mainColor) 0 0 1px, var(--mainColor) 0 0 1px, var(--mainColor) 0 0 1px, var(--mainColor) 0 0 1px;
    z-index: -1;
    filter: blur(1px);
    -webkit-filter: blur(1px);
}

.after {
    &::after {
        opacity: 0.5;
        visibility: visible;
        bottom: -7px;
        right: -6px;
    }
}

.bouncing-letter {
    visibility: hidden;
}

@keyframes bounce {
    0% {
        visibility: visible;
        transform: scale(1,1) translateY(0);
    }
    10% {
        transform: scale(1.1,.9) translateY(0);
    }
    30% {
        transform: scale(.9,1.1) translateY(-15px);
    }
    50% {
        transform: scale(1.05,.95) translateY(0);
    }
    58% {
        transform: scale(1,1) translateY(-7px);
    }
    65% {
        transform: scale(1,1) translateY(0);
    }
    100% {
        transform: scale(1,1) translateY(0);
        visibility: visible;
        transition: unset;
    }
}

@-webkit-keyframes bounce {
    0% {
        visibility: visible;
        transform: scale(1,1) translateY(0);
    }
    10% {
        transform: scale(1.1,.9) translateY(0);
    }
    30% {
        transform: scale(.9,1.1) translateY(-15px);
    }
    50% {
        transform: scale(1.05,.95) translateY(0);
    }
    58% {
        transform: scale(1,1) translateY(-7px);
    }
    65% {
        transform: scale(1,1) translateY(0);
    }
    100% {
        transform: scale(1,1) translateY(0);
        visibility: visible;
        transition: unset;
    }
}

@keyframes shake {
    0% {
        visibility: visible;
        transform: rotate(10deg);
    }
    10% {
        transform: rotate(-10deg);
    }
    30% {
        transform: rotate(10deg);
    }
    50% {
        transform: rotate(-10deg);
    }
    60% {
        transform: rotate(10deg);
    }
    80% {
        transform: rotate(-10deg);
    }
    100% {
        rotate: 10deg;
        transition: unset;
    }
}

@-webkit-keyframes shake {
    0% {
        visibility: visible;
        transform: rotate(10deg);
    }
    10% {
        transform: rotate(-10deg);
    }
    30% {
        transform: rotate(10deg);
    }
    50% {
        transform: rotate(-10deg);
    }
    60% {
        transform: rotate(10deg);
    }
    80% {
        transform: rotate(-10deg);
    }
    100% {
        rotate: 10deg;
        transition: unset;
    }
}

.prestation-price {
    visibility: hidden;
    padding: 0 7.5%;
    margin-left: calc(-30% - 2vw);
    rotate: 30deg;
    font-weight: bold;
    font-size: 1.5rem;
    width: 4.5rem;
    box-shadow: 3px 3px 3px var(--mainColor);
    color: var(--mainColorLight);
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    background-color: var(--mainColor);
    z-index: -1;
    text-align: center;
}

.prestation-price::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 90%;
    transform: translate(-50%, -50%);
    width: 0.20rem;
    height: 0.20rem;
    box-shadow: 3px 3px 3px var(--mainColorLight);
    border-radius: 50%;
}

footer {
    font-size: small;
    bottom: 0;
    position: absolute;
    background-color: var(--mainColor);
    width: 100%;
    height: 17.5vh;
    display: inline-flex;
    flex-direction: row;
    flex: 1 1 0px;
    color: var(--mainColorLight);
    align-items: center;
    justify-content: center;
    padding: 0 2%;
    text-align: center;
}

footer > div {
    width: 50%;
}

.notfound #what {
    text-align: center;
    font-size: 5rem;
    color: var(--mainColorLight);
    background-color: var(--mainColor);
    line-height: 5rem;
    width: 5rem;
    height: 5rem;
    border-radius: 50px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.notfound #home {
    &:hover {
        cursor: pointer;
    }
    text-align: center;
    font-size: 1rem;
    color: var(--mainColor);
    background-color: unset;
    line-height: 2rem;
    text-decoration: underline;
    border-radius: 5px;
    font-family: Arial, Helvetica, sans-serif;
    width: 11rem;
    border: unset;
}

.notfound {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    height: 65vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#client {
    margin-top: 2.5%;
    align-items: center;
    display: inline-flex;
    gap: 5%;
    flex-direction: column;
    border: 1px yellow solid;
    width: 100vw;
    height: 100vh;
}

#client > div {
    width: 40vw;
    height: 40vw;
}

#client #infos {
    border: 1px red solid;
}

#client #appointment {
    border: 1px blue solid;
}
